import React, { Component } from 'react';

export default class Preloader extends Component {
  preloaderEl = React.createRef();

  preloaderChildren = {
    title: React.createRef(),
    dash: React.createRef(),
    explanation: React.createRef()
  };

  componentDidMount(){
    if(this.preloaderEl !== null){
      this.startPreloader();
    }
  }

  componentDidUpdate(){
    if(this.props.heroVideoLoaded === true){
      setTimeout(() => {
        this.endPreloader();
      }, 1000)
    }
  }

  componentWillUnmount(){
    clearInterval(this.preloaderInterval);
  }

  startPreloader(){
    if(this.preloaderChildren.title.current === null){
      return;
    }

    this.preloadTimer = new Date().getTime();

    let preloadList = {
      "titles":[
        "pepeha",
        "maunga",
        "moana",
        "Awa",
        "roto",
        "rohe",
        "WhĀnau",
        "au",
        "waka",
        "marae",
        "tĀngata",
        "iwi"
      ],

      "explanations":[
        "who i am",
        "mountain",
        "ocean",
        "river or stream",
        "lake",
        "region",
        "family",
        "me",
        "canoe or ship",
        "meeting house",
        "people",
        "tribe"
      ]
    }

    this.unusedList = preloadList;

    if(!this.preloaderChildren.title.current.classList.contains("-active")){
      this.preloaderLoop();
    }

    this.preloaderInterval = setInterval(() => { this.preloaderLoop() }, 5000);
  }

  preloaderLoop(){
    if(this.preloaderChildren.title.current === null){
      return;
    }

    if(this.unusedList.titles.length > 0){
      let timing = 700;

      if(!this.preloaderChildren.title.current.classList.contains("-active")){
        timing = 0;
      }

      let rand = Math.floor(Math.random() * this.unusedList.titles.length);

      this.preloaderChildren.title.current.classList.remove("-active");
      this.preloaderChildren.dash.current.classList.remove("-active");
      this.preloaderChildren.explanation.current.classList.remove("-active");

      let timeElapsed = Date.now() - this.preloadTimer;

      if(timeElapsed > 10000){
        this.endPreloader();
      }

      setTimeout(() => {
        this.preloaderChildren.title.current.innerHTML = this.unusedList.titles[rand];
        this.preloaderChildren.explanation.current.innerHTML = this.unusedList.explanations[rand];

        this.preloaderChildren.title.current.classList.add("-active");
        this.preloaderChildren.dash.current.classList.add("-active");
        this.preloaderChildren.explanation.current.classList.add("-active");

        this.unusedList.titles.splice(rand, 1);
        this.unusedList.explanations.splice(rand, 1);
      },timing)
    } else {
      clearInterval(this.preloaderInterval);
      this.startPreloader();
    }
  }

  endPreloader(){
    this.preloaderEl.current.classList.remove("-active");
    clearInterval(this.preloaderInterval);

    setTimeout(() => {
      this.props.preloaderDone();
    }, 1000)
  }

  render() {
    return (
      <div ref={this.preloaderEl} className="preloader -active">
        <div className="preloader-content">
          <p ref={this.preloaderChildren.title} className="preloader-title"></p>
          <p ref={this.preloaderChildren.dash} className="preloader-dash">-</p>
          <p ref={this.preloaderChildren.explanation} className="preloader-explanation"></p>
        </div>
      </div>
    );
  }
}

