import React, { Component } from 'react';
import { TweenLite, Sine } from "gsap/TweenMax";


export default class Pronounciation extends Component {
  vowels = [
    {
      "maori":"A",
      "phonetics": "AH"
    },
    {
      "maori":"E",
      "phonetics": "EAR"
    },
    {
      "maori":"I",
      "phonetics": "EE"
    },
    {
      "maori":"O",
      "phonetics": "OR"
    },
    {
      "maori":"U",
      "phonetics": "EW"
    },
    {
      "maori":"Ā",
      "phonetics": "AHH"
    },
    {
      "maori":"Ē",
      "phonetics": "EARR"
    },
    {
      "maori":"Ī",
      "phonetics": "EEE"
    },
    {
      "maori":"Ō",
      "phonetics": "ORR"
    },
    {
      "maori":"Ū",
      "phonetics": "EWW"
    }
  ];

  words = [
    {
      "maori":"KO",
      "phonetics":"COR"
    },
    {
      "maori":"TE",
      "phonetics":"TEH"
    },
    {
      "maori":"AWA",
      "phonetics":"AH-WAH"
    },
    {
      "maori":"AHAU",
      "phonetics":"A-HOE"
    },
    {
      "maori":"WAKA",
      "phonetics":"WAH-KAH"
    },
    {
      "maori":"TŌKU",
      "phonetics":"TOR-KU"
    },
    // {
    //   "maori":"HAPŪ",
    //   "phonetics":"Lorem"
    // },
    {
      "maori":"MAUNGA",
      "phonetics":"MOW-NGA"
    },
    {
      "maori":"WHĀNAU",
      "phonetics":"FAR-NO"
    },
    {
      "maori":"NŌ",
      "phonetics":"NOR"
    },
    {
      "maori":"Taku",
      "phonetics":"TAH-KU"
    },
    {
      "maori":"INGOA",
      "phonetics":"ING-OA"
    }
  ]

  componentDidMount(){
    let soundIcon = `
      <svg class="soundIcon" x="0px" y="0px" viewBox="0 0 40.5 71.2" xml:space="preserve">
        <path class="wave3" d="M26.9,71.2l-5.1-4.8c7.9-8.4,12.1-19.5,11.7-31C33.1,23.9,28.1,13,19.6,5.2L24.4,0C34.2,9.1,40,21.7,40.4,35.1C40.9,48.5,36,61.5,26.9,71.2z"/>
        <path class="wave2" d="M16.5,61.1l-5.1-4.8c10.9-11.8,10.2-30.2-1.6-41.2l4.8-5.1C29.1,23.6,30,46.4,16.5,61.1z"/>
        <path class="wave1" d="M6,51.1l-2.7-2.7l0.2,0.2l-2.6-2.4l0,0C6.5,40.2,6.1,30.8,0,25.2L4.7,20c8.9,8.2,9.5,22.1,1.4,31L6,51.1z"/>
      </svg>
    `;

    this.soundIcon = document.querySelectorAll(".soundIcon");
    this.soundText = document.querySelectorAll(".home-pronounciation-letter, .home-pronounciation-word");

    this.soundIcon.forEach(el => {
      el.innerHTML = soundIcon;
    })

    this.soundAnimation = (wave1,wave2,wave3,timing) => {
      const opacity = 0.5;

      TweenLite.to([wave1,wave2,wave3], 0, {opacity: opacity});

      TweenLite.to(wave1, (timing / 4), {opacity: 1, ease: Sine.easeInOut, onComplete: () => {
        if(this.soundAnimationInterval === null) TweenLite.to([wave1,wave2,wave3], 0, {opacity: 1});

        TweenLite.to(wave2, (timing / 4), {opacity: 1, ease: Sine.easeInOut, onComplete: () => {
          if(this.soundAnimationInterval === null) TweenLite.to([wave1,wave2,wave3], 0, {opacity: 1});
          
          TweenLite.to(wave3, (timing / 4), {opacity: 1, ease: Sine.easeInOut});
        }});
      }});
    }

    this.soundText.forEach((el, i) => {
      let audio = el.querySelector("audio");

      if(audio !== null){
        const wave1 = el.querySelector(".wave1"),
              wave2 = el.querySelector(".wave2"),
              wave3 = el.querySelector(".wave3"),
              timing = 1.5;

        audio.addEventListener("playing", () => {
          el.classList.add("-playing");

          this.soundAnimation(wave1,wave2,wave3,timing);

          if(this.soundAnimationInterval !== null) clearInterval(this.soundAnimationInterval);

          this.soundAnimationInterval = setInterval(() => {
            this.soundAnimation(wave1,wave2,wave3,timing);
          }, timing * 1000)
        });

        audio.addEventListener("ended", () => {
          el.classList.remove("-playing");
          clearInterval(this.soundAnimationInterval);
          this.soundAnimationInterval = null;
        });

        el.addEventListener("click", () => {
          audio.play();
        })
      }
    })
  }

  render() {
    return(
      <div className="home-pronounciation">
        <h3 className="home-pronounciation-title">Pronunciation</h3>
        <p className="home-pronounciation-paragraph">Here’s a few tips of how to pronounce some of the key words within your Pepeha. For audio pronunciation click the <i className="soundIcon"></i> symbol. Don't forget your macrons. To activate your macrons, hold down the vowel key and a set of macrons will&nbsp;appear.</p>
        <div className="home-pronounciation-vowels">
          {this.vowels.map(vowel => {
            return(
              <div className="home-pronounciation-letter" key={vowel.maori}>
                <p className="home-pronounciation-maori">{vowel.maori}</p>
                <p className="home-pronounciation-phonetics">{vowel.phonetics} <i className="soundIcon"></i></p>
                <audio>
                  <source src={`./home/sounds/${vowel.phonetics}.mp3`} type="audio/mpeg"/>
                </audio>
              </div>
            )
          })}
        </div>
        <div className="home-pronounciation-words">
          {this.words.map(word => {
            return(
              <div className="home-pronounciation-word" key={word.maori}>
                <p className="home-pronounciation-maori">{word.maori}</p>
                <p className="home-pronounciation-phonetics">{word.phonetics} <i className="soundIcon"></i></p>
                <audio>
                  <source src={`./home/sounds/${word.phonetics}.mp3`} type="audio/mpeg"/>
                </audio>
              </div>
            )
          })}
        </div>
      </div>
    )
  }
}